import React from "react"

export default function Comments({comments=[]}){
    var classOfList = comments.length > 0 ? 'd-block p-0' : 'd-none'
    return <ul key='comments' className={classOfList} style={{listStyle:"none",margin:'0 auto'}}>
        <h2>Comments</h2>
        {
            comments.map(comment=>{
                // console.log(`post`, post);
                return (
                <li key={comment.id} className='card my-3'>
                    <div className="card-body text-capitalize">
                        <h4 className="card-title">{comment.name}</h4>
                        <p className="card-text">{comment.body}</p>
                    </div>
                </li>)
            })
        }
    </ul>
}


